.footer {
  background-color: #000000;

  padding-top: 90px;
  padding-bottom: 77px;
}

.footer--inner-page {
  margin-bottom: 0;
}

.footer__container {
  display: flex;
  justify-content: space-between;
}

.footer__form {
  display: flex;

  border-bottom: 1px solid #ffffff;

  padding-bottom: 10px;
}

.footer__form input {
  height: 21px;
  width: 90%;
  margin-right: 10px;

  font-size: 14px;
  line-height: 21px;

  color: #ffffff;

  border: none;
  background-color: #000000;
}

.footer__form input:hover {
  opacity: 0.6;
}

.footer__form input:focus {
  opacity: 1;

  outline: none;
}

.footer__form input::placeholder {
  font-family: "Poppins", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  color: #9b9997;
}

.footer__form-button {
  font-family: "Poppins", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  color: #ffffff;
  text-align: left;

  background-color: #000000;
  background-image: url("data:image/svg+xml,%3Csvg width='30' height='11' viewBox='0 0 30 11' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 5.5H29M29 5.5L23.5135 1M29 5.5L23.5135 10' stroke='white'/%3E%3C/svg%3E%0A");
  background-position: right center;
  background-repeat: no-repeat;
  border: none;

  min-width: 110px;
}

.footer__form-input-container {
  width: 100%;
}

.footer__nav-item {
  margin-bottom: 20px;
}

.footer__nav-list {
  @include listReset;

  margin-right: 10px;
  padding-top: 7px;
}

.footer__nav-link {
  color: #ffffff;
}

.footer__nav-list--left {
  width: 21%;
}

.footer__nav-list--right {
  width: 16.5%;
}

.footer__social-item {
  margin-bottom: 20px;

  width: 100%;
}

.footer__social-link {
  display: block;

  color: #ffffff;

  padding-left: 32px;

  background-repeat: no-repeat;
  background-position: left center;
}

.footer__social-link--facebook {
  background-image: url("data:image/svg+xml,%3Csvg width='8' height='15' viewBox='0 0 8 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.33333 8.625H7.2381L8 5.625H5.33333V4.125C5.33333 3.3525 5.33333 2.625 6.85714 2.625H8V0.105C7.75162 0.0727501 6.81371 0 5.82324 0C3.75467 0 2.28571 1.24275 2.28571 3.525V5.625H0V8.625H2.28571V15H5.33333V8.625Z' fill='white'/%3E%3C/svg%3E%0A");
  background-position: 5px center;
}

