.footer__social-list {
  @include listReset;
  width: 117px;

  padding-top: 7px;
}

.footer__top-container {
  width: 43%;
  margin-right: auto;
}

.footer__top-container h2 {
  font-family: "Cormorant Garamond", "Arial", serif;
  font-style: normal;
  font-weight: 700;
  font-size: 34px;
  line-height: 44px;
  color: #ffffff;
  text-align: left;
  letter-spacing: 0.03em;

  margin: 0;
  margin-bottom: 10px;
  padding: 0;
}

.footer__top-container p {
  color: #9b9997;

  margin-bottom: 40px;
}

@media (max-width: $desktop-res) {
  .footer {
    padding-top: 50px;
    padding-bottom: 37px;

    min-height: 447px;
  }

  .footer__container {
    flex-wrap: wrap;

    width: 85%;
    margin-left: 0;
    margin-right: auto;
  }

  .footer__nav-list--left {
    width: 39%;
    margin-right: auto;
  }

  .footer__social-list {
    width: 105px;
  }

  .footer__nav-list--right {
    width: 29%;

    margin-right: auto;
  }

  .footer__top-container {
    width: 100%;

    margin-bottom: 40px;
  }
}

@media (max-width: $tablet-res) {
  .footer {
    padding-bottom: 50px;

    min-height: 772px;
  }

  .footer__nav-list--left,
  .footer__nav-list--right,
  .footer__social-list,
  .footer__container {
    width: 100%;
    margin-right: 0;
  }

  .footer__top-container {
    margin-bottom: 35px;
  }

  .footer__top-container h2 {
    padding-right: 50px;
    margin-bottom: 15px;
  }

  .footer__top-container p {
    padding-right: 50px;
  }

  .footer__nav-item {
    margin-bottom: 22px;
  }

  .footer__nav-list--left,
  .footer__nav-list--right {
    margin-bottom: 10px;
  }

  .footer__social-item {
    margin-right: 27px;
    margin-bottom: 10px;

    width: 20px;
    height: 18px;

    &:nth-child(4n) {
      margin-right: 0;
    }
  }

  .footer__social-item--facebook {
    margin-right: 20px;
  }

  .footer__social-item--instagramm {
    margin-right: 35px;
  }

  .footer__social-link {
    font-size: 0;

    padding-left: 0;

    width: 100%;
    height: 100%;

    background-position: center;
    background-size: cover;
  }

  .footer__social-link--facebook {
    width: 12px;
    height: 22px;
  }

  .footer__social-link--twitter {
    height: 18px;
  }

  .footer__social-list {
    width: 200px;

    display: flex;
    flex-wrap: wrap;
    align-items: center;

    padding-top: 12px;
  }
}
